<template>
    <div>
        <br>
        <div class="row">
            <div class="offset-1 col-9  bg-white p-5 rounded-lg shadow-lg">
                <p class="pl-5 font-bold font-times text-2xl font-bold">VISI DAN MISI</p>
                <p class="p-4 text-justify font-times text-xl" v-html="visi">
                </p>
            </div>
        </div>
        <br>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    data(){
        return{
            visi:""      
        }
    },
    layout:"awal",
    mounted() {
        let fd = new FormData()
        fd.append('data', `select * from sekolah`)//database setting di server
        fd.append('database','infolay3_test')
        axios.post('https://infolayanans.space/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log(res)
            this.visi=res.data[0].visi
            this.$forceUpdate()
        })
    },
}
</script>